"use client";

import { useState, FormEvent } from "react";
import { FiSend } from "react-icons/fi";

import { InputField } from "@/components/common/InputField";
import { TextareaField } from "@/components/common/TextareaField";
import { Button } from "@/components/ui/Button";

interface FormField {
  name: string;
  label: string;
  type: string;
  placeholder: string;
  required: boolean;
  rows?: number;
}

interface ContactFormProps {
  fields: FormField[];
  submitLabel: string;
}

export const ContactForm = ({ fields, submitLabel }: ContactFormProps) => {
  const [formData, setFormData] = useState<Record<string, string>>({});
  const [isSubmitting, setIsSubmitting] = useState(false);

  const handleChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value,
    });
  };

  const handleSubmit = async (e: FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    setIsSubmitting(true);

    setTimeout(() => {
      setIsSubmitting(false);
    }, 1000);
  };

  return (
    <form onSubmit={handleSubmit} className="flex flex-col gap-6">
      {fields.map((field) => (
        <div key={field.name} className="flex flex-col gap-2">
          <Label htmlFor={field.name}>
            {field.label}
            {field.required && <span className="text-blue-500 ml-1">*</span>}
          </Label>
          {field.type === "textarea" ? (
            <Textarea
              id={field.name}
              name={field.name}
              rows={field.rows || 5}
              placeholder={field.placeholder}
              required={field.required}
              value={formData[field.name] || ""}
              onChange={handleChange}
            />
          ) : (
            <Input
              id={field.name}
              name={field.name}
              type={field.type}
              placeholder={field.placeholder}
              required={field.required}
              value={formData[field.name] || ""}
              onChange={handleChange}
            />
          )}
        </div>
      ))}

      <Button
        type="submit"
        disabled={isSubmitting}
        className="w-full group flex items-center justify-center gap-2"
      >
        {submitLabel}
        <FiSend className="w-5 h-5 group-hover:translate-x-1 transition-transform" />
      </Button>
    </form>
  );
};
